import React, { useState, useEffect } from 'react';
import Proptypes from 'prop-types';
import { getFormbuilderByIdService } from '../../services/form';
import { map } from 'lodash';
import ReactHtmlParser from 'react-html-parser';

const propTypes = {
  data: Proptypes.array.isRequired,
  getPageBlock: Proptypes.func
};

function MenuIntro({ data }) {
  const [formdata, setFormData] = useState({});
  const getFormByID = async () => {
    const res = await getFormbuilderByIdService(Number(data.formdata));
    if (res && res.status === 200) {
      setFormData(res.data);
    }
  };

  useEffect(() => {
    getFormByID();
  }, []);
  return (
    <div>
      <section className=" sec-menu">
        <div className="container">
          <ul>
            <li className="active">
              <a href="#">Gii thiu</a>
            </li>
            {map(data.listBlock, (values, index) => (
              <li key={index}>
                <a href={`#${values.id}`}>{values.title}</a>
              </li>
            ))}
          </ul>
        </div>
      </section>
      <section className=" sec-tb sec-ab-1">
        <div className="container">
          <div className="row list-item ">
            <div className="col-lg-8">
              <div className="boxwidget">
                <h2 className="widget-title">GII THIU</h2>
                <p>
                  App MBBank l ng dng ngn hng ca MB trn in thoi di ng, cho php khch
                  hng thc hin hu ht giao dch ti chnh, thanh ton hng ngy vi thao tc n
                  gin, thc hin c mi lc, mi ni. L ng dng c ci t trn in thoi
                  thng minh nn thng xuyn c nng cp, mang n tri nghim v s thun tin
                  ti u nht cho khch hng{' '}
                </p>
              </div>
              <div className="boxwidget-2">
                <p>
                  Nu bn mun c thm thng tin v sn phm Vay siu nhanh, hy lin h <br /> vi
                  chng ti qua:
                </p>
                <div className="fs16 cl1">1900 545426 - (84-24) 3767 4050 (quc t gi v)</div>
              </div>
            </div>
            <div className="col-lg-4 sidebar">
              <div className="widget widget-tuvan">
                <div>{ReactHtmlParser(formdata.embedded)}</div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

MenuIntro.propTypes = propTypes;

export default MenuIntro;
